// React Required
import React, { Component } from 'react';
import ReactDOM from 'react-dom';

// Create Import File
import './index.scss';

// Common Layout
// import Layout from "./component/common/App";

import PageScrollTop from './component/PageScrollTop';

// Home layout
import Main from './home/Main';

// Dark Home Layout 

// Element Layout
import Service from "./elements/Service";
import ServiceDetails from "./elements/ServiceDetails";
import WebDevelopment from "./elements/WebDevelopment";
import MobileAppDevelopment from "./elements/MobileAppDevelopment";
import Smarthome from "./elements/Smarthome";

import About from "./elements/About";
import Contact from "./elements/Contact";
import Quote from "./elements/quote";
import PortfolioDetails from "./elements/PortfolioDetails";
import error404 from "./elements/error404";
 import 'react-toastify/dist/ReactToastify.min.css';
import { BrowserRouter, Switch, Route  } from 'react-router-dom';
import * as serviceWorker from './serviceWorker';
import { ToastContainer } from 'react-toastify';

class Root extends Component{
    render(){
        return(
            <BrowserRouter basename={'/'}>
            <ToastContainer />
                <PageScrollTop>
                    <Switch>
                        <Route exact path={`${process.env.PUBLIC_URL}/`} component={Main}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/service`} component={Service}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/service-details`} component={ServiceDetails}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/web-development`} component={WebDevelopment}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/mobile-app-development`} component={MobileAppDevelopment}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/smart-home`} component={Smarthome}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/contact`} component={Contact}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/get-quote`} component={Quote}/>
                        <Route exact path={`${process.env.PUBLIC_URL}/about`} component={About}/>
                        <Route path={`${process.env.PUBLIC_URL}/404`} component={error404}/>
                        <Route component={error404}/>

                    </Switch>
                </PageScrollTop>
            </BrowserRouter>
        )
    }
}

ReactDOM.render(<Root/>, document.getElementById('root'));
serviceWorker.register();