import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import { FiCheck } from "react-icons/fi";

class TabsOne extends Component{
    render(){
        let 
        tab1 = "Our Mission", 
        tab2 = "Our Core Purpose";
        const { tabStyle } = this.props;
        var namesItemOne = [
            'Absolute Integrity',
            'Customer Satisfaction',
        ];
        return(
            <div>
                {/* Start Tabs Area */}
                <div className="tabs-area">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <Tabs>
                                    <TabList  className={`${tabStyle}`}>
                                        <Tab>{tab1}</Tab>
                                        <Tab>{tab2}</Tab>
                                    </TabList>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                           <p>To provide cost-effective solutions and services that fit our client's needs and bring them to actualization.</p>
                                       </div>
                                    </TabPanel>


                                    <TabPanel>
                                       <div className="single-tab-content">
                                            <p>To improve the quality of life through technological innovations</p>

                                           {/*<div className="mt--30 mt_lg--10 mt_md--10 mt_sm--10">
                                               <ul className="list-style--1">
                                                   {namesItemOne.map((name, index) => {
                                                       return <li key={ index }><FiCheck /> {name}</li>;
                                                   })}
                                               </ul>
                                           </div>*/}
                                       </div>
                                    </TabPanel>
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Tabs Area */}
            </div>
        )
    }
}
export default TabsOne;