import React, { Component } from "react";
import WebQuote from "./WebQuote";
import MobileQuote from './MobileQuote';
import Select from 'react-select';

const options = [
            { value: 'web', label: 'Web Development' },
            { value: 'mobile', label: 'Mobile App Development' },
            { value: 'home', label: 'Smart Home Technologies' },
            { value: 'marketing', label: 'Digital Marketing' }
        ]
class QuoteOne extends Component{

   constructor(props) {
    super(props);
    this.state = {service: 'web', name: 'Web Development'};
  }

    render(){
        return(
            <div className="contact-form--1">
                <div className="container">
                    <div className="row row--35 align-items-start">
                        <div className="col-lg-6 order-2 order-lg-1">
                            <div className="section-title text-left mb--50">
                                <h2 className="title">{ this.state.service !== '' ? "Get Quote for " + this.state.name : "Select Service Category" }</h2>
                                <div className="im_address_inner">

                                </div>
                            </div>
                            <Select options={options} defaultValue={options[0]} isClearable={false} onChange={(data)=>{this.setState({service: data.value, name: data.label});}} className="rn-form-group"/>
                            <br/>{
                                this.state.service == "web" ? 
                                <WebQuote />: this.state.service === "mobile" ? 
                                <MobileQuote/>:
                                null
                            }
                        </div>
                        <div className="col-lg-6 order-1 order-lg-2">
                            <div className="thumbnail mb_md--30 mb_sm--30">
                                <img src="/assets/images/about/about-1.webp" alt="contact"/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default QuoteOne;