import React , {useState} from 'react';
import Select from 'react-select';
import ReactJsAlert from "reactjs-alert"
import { toast } from 'react-toastify';

const options = [
            { value: 'Business', label: 'Business' },
            { value: 'Ecommerce', label: 'Ecommerce' },
            { value: 'Messaging', label: 'Messaging' },
            { value: 'Social media', label: 'Social media' },
            { value: 'Delivery App', label: 'Delivery App' },
            { value: 'News App', label: 'News App' } 
        ]
const Result = () => {
    return (
        <p className="success-message">Your Message has been successfully sent. I will contact you soon.</p>
    )
}
function MobileQuote({props}) {
    const [ result,showresult ] = useState(false);
    const [ error,setError ] = useState(null);
    const [ type,setType ] = useState(null);
    const [ fullname,setFullname ] = useState(null);
    const [ email,setEmail] = useState(null);
    const [ phone,setPhone] = useState(null);
    const [ deadline,setDeadline] = useState(null);
    const [ template,setTemplate] = useState(null);
    const [ message,setMessage] = useState(null);


    setTimeout(() => {
        showresult(false);
    }, 5000);


    const handleFormSubmit = (event) =>{
        if(!fullname){
            toast.warn("Please enter your fullname.!", {
                position: toast.POSITION.TOP_CENTER
            });
          return ;
        }

       else if(!email){

            toast.warn("Please enter your email address.!", {
                position: toast.POSITION.TOP_CENTER
            });
          return ;
        }

        else if(!phone){
            toast.warn("Please enter your phone number.!", {
                position: toast.POSITION.TOP_CENTER
            });
          return ;
        }

        else if(!deadline){
            toast.warn("Please enter your deadline.!", {
                position: toast.POSITION.TOP_CENTER
            });
          return ;
        }

        else if(!type){
            toast.warn("Please enter App category!", {
                position: toast.POSITION.TOP_CENTER
            });
          return ;
        }
        else{

            event.preventDefault();

            let formBody = {fullname: fullname, email: email, phone:phone, deadline:deadline, message:message, template:template, type:type, category: "Mobile App Development"}
            console.log(formBody);

            fetch('https://vidarave.com.ng/forms/contact.php', {
            method: 'post',
            headers: { 'Content-Type': 'application/json' },
            body: formBody,
           })
           .then((response) => response.json())
           .then((responseJson) => {
             if (responseJson.sent === true){
                 showresult(true);
             }
           })
           .catch((error) => {
             console.error(error);
           });
       }
    }

    
    return (
        <div className="form-wrapper">
            <form action="">
                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="fullname"
                    placeholder="Your Name"
                    onChange={(e) => setFullname(e.target.value)}
                    required
                    />
                </div>

                <div className="rn-form-group">
                    <input 
                    type="email"
                    name="email"
                    placeholder="Your Email"
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    />
                </div>

                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="phone"
                    placeholder="Phone Number"
                    onChange={(e) => setPhone(e.target.value)}
                    required
                    />
                </div>

                <Select options={options} placeholder="App category" isClearable={true} onChange={(data)=>{setType(data.value);}} className="rn-form-group"/>
                <br/>
                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="deadline"
                    placeholder="Project Deadline"
                    onChange={(e) => setDeadline(e.target.value)}
                    required
                    />
                </div>


                <div className="rn-form-group">
                    <input 
                    type="url"
                    name="template"
                    placeholder="Url of Template or an example of what you have in mind"
                    onChange={(e) => setTemplate(e.target.value)}
                    
                    />
                </div>

                
                <div className="rn-form-group">
                    <textarea 
                    name="message"
                    placeholder="Additional Notes"
                    onChange={(e) => setMessage(e.target.value)}
                    >
                    </textarea>
                </div>

                <div className="rn-form-group">
                    <button className="btn-default" onClick={e => handleFormSubmit(e)} >Submit Now</button>
                </div> 

                <div className="rn-form-group">
                    {result ? <Result /> : null}
                </div> 
            </form>
        </div>
    )
}
export default MobileQuote;