import React , {useState} from 'react';

const Result = () => {
    return (
        <p className="success-message">Your request has been successfully sent. I will contact you soon.</p>
    )
}
function FormStyle({props}) {
    const [ result,showresult ] = useState(false);
    const [ fullname,setFullname ] = useState('');
    const [ email,setEmail] = useState('');
    const [ phone,setPhone] = useState('');
    const [ subject,setSubject] = useState('');
    const [ message,setMessage] = useState('');


    setTimeout(() => {
        showresult(false);
    }, 5000);


    const handleFormSubmit = (event) =>{
        event.preventDefault();

        let formBody = {fullname: fullname, email: email, phone:phone, subject:subject, message:message};
        formBody = JSON.stringify(formBody);
        //console.log(formBody);

        fetch('https://vidarave.com.ng/forms/contact.php', {
        method: 'post',
        headers: { 'Content-Type': 'application/json' },
        body: formBody,
       })
       .then((response) => response.json())
       .then((responseJson) => {
         if (responseJson.sent == true){
             showresult(true);
         }
       })
       .catch((error) => {
         console.error(error);
       });
    }

    
    return (
        <div className="form-wrapper">
            <form action="">
                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="fullname"
                    placeholder="Your Name"
                    onChange={(e) => setFullname(e.target.value)}
                    required
                    />
                </div>

                <div className="rn-form-group">
                    <input 
                    type="email"
                    name="email"
                    placeholder="Your Email"
                    onChange={(e) => setEmail(e.target.value)}
                    required
                    />
                </div>

                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="phone"
                    placeholder="Phone Number"
                    onChange={(e) => setPhone(e.target.value)}
                    required
                    />
                </div>

                <div className="rn-form-group">
                    <input 
                    type="text"
                    name="subject"
                    placeholder="Subject"
                    onChange={(e) => setSubject(e.target.value)}
                    required
                    />
                </div>
                
                <div className="rn-form-group">
                    <textarea 
                    name="message"
                    placeholder="Your Message"
                    onChange={(e) => setMessage(e.target.value)}
                    required
                    >
                    </textarea>
                </div>

                <div className="rn-form-group">
                    <button className="btn-default" onClick={e => handleFormSubmit(e)} >Submit Now</button>
                </div> 

                <div className="rn-form-group">
                    {result ? <Result /> : null}
                </div> 
            </form>
        </div>
    )
}
export default FormStyle;