import React ,{ Component }from "react";
import { FcHome, FcSmartphoneTablet, FcGlobe } from "react-icons/fc";

const ServiceList = [
    {
        icon: <FcGlobe />,
        link: 'web-development',
        title: 'Web Design and Development',
        description: 'Do you want to build your brand, broaden your reach and maximize your revenue? Create your website with the finest web development company in town.'
    },

    {
        icon: <FcSmartphoneTablet />,
        link: 'mobile-app-development',
        title: 'Mobile App development',
        description: 'We design and develop mobile apps for both the Android and iOS operating systems. We have a team of professional mobile developers who are experts in their sector and will assist you in getting your concept live on the app stores.'
    },
    {
        icon: <FcHome />,
        link: 'smart-home',
        title: 'Smart Home and Automation',
        description: 'A smart home allows you to manage your entire electronic life from one location. Smart homes let you monitor and control your security system, home entertainment system, lights, locks, heating and cooling systems by voice commands or touch of a button'
    }
]


class ServiceThree extends Component{
    render(){
        const {column } = this.props;
        const ServiceContent = ServiceList.slice(0 , this.props.item);
        
        return(
            <React.Fragment>
                <div className="row service-main-wrapper">
                    {ServiceContent.map( (val , i) => (
                        <div className={`${column}`} key={i}>
                            <a href={`/${val.link}`}>
                                <div className="service service__style--2 text-left bg-gray">
                                    <div className="icon">
                                        {val.icon}
                                    </div>
                                    <div className="content">
                                        <h3 className="title">{val.title}</h3>
                                        <p style={{color: '#444'}}>{val.description}</p>
                                    </div>
                                </div>
                            </a>
                        </div>
                    ))}
                </div>
            </React.Fragment>
        )
    }
}
export default ServiceThree;
